.container-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    text-align: center;
}

.container-carrocel {
    position: relative;
    text-align: center;
    width: 256px;
    height: 192px;
    margin: .6%;
    cursor: pointer;
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
}

.text-not-image {
    text-align: center;
    color: #C4C4C4;
    padding: 5em;
    font-weight: 900;
}